.Widgets
{
    flex: 0.25;
}
.Widgets--Input
{
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}
.Widgets--Input > input
{
    border: none;
    background-color: var(--twitter-background);
}
.Widgets--SearchIcon
{
    color: gray;
}
.Widgets--WidgetContainer
{
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background-color: #f5f8fa;
    border-radius: 20px;
}
.Widgets--WidgetContainer > h2
{
    font-size: 18px;
    font-weight: 800;
}


