*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body
{
    --twitter-color:#50b7f5;
    --twitter-background:#e6ecf0;   
}
.app
{
    display: flex;
    height: 100vh;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}
.Feed
{
    flex: 0.5 1;
    border-right:1px solid var( --twitter-background);
    height: 100vh;
    min-height: 1300px;
    overflow-y: scroll;
}
.Feed::-webkit-scrollbar
{
    display: none;
}
.Feed--header
{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    border: 1px solid var(--twitter-background);
    padding: 15px 20px;
}
.Feed--header>h2
{
    font-size: 20px;
    font-weight: 800;
}

.Post
{
    display: flex;
    align-items: flex-start;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--twitter-background);
}
.Post--Body
{
    flex: 1 1;
    padding: 10px;
    
}
.Post--avatar
{
 padding: 20px;
}
.Post--Body > img
{ 
 border-radius: 20px;
 max-width: 100%;
 margin: auto;
}
.Post--HeaderSpecial
{
 font-weight: 600;
 font-size: 12px;
 color: gray;    
}
.Post--HeaderText >h3
{ 
 font-size: 15px;
 margin-bottom: 5px;
}
.Post--HeaderDescription
{
    margin-bottom: 10px;
    font-size: 15px;
}
.Post--Footer
{
    display: flex;
    justify-content:space-between;
    margin-top: 10px;
}
.Post--Badge
{   
    font-size: 14px !important;
    color: var(--twitter-color);
}
.TweetBox
{
    padding-bottom: 10px;
    border-bottom: 8px solid var(--twitter-background);
    padding-right: 10px;
}
.TweetBox--tweet
{
    background-color: var(--twitter-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    width: 80px !important;
    margin: auto!important;
}
.TweetBox > form
{
 display: flex;
 flex-direction: column;
}
.TweetBox--input
{
 padding: 20px;
 display: flex;
}
.TweetBox--input > input
{
   flex: 1 1; 
   margin-left: 20px;
   font-size: 20px;
   border: none;
   border-radius: 20px;
}
.TweetBox--Imageinput
{
    border: none;
    padding: 10px;
}





.SidebarOption
{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.SidebarOption:hover
{
 background-color: #e8f5fe;   
 border-radius: 30px;
 width: 100%;
 color: var(--twitter-color);
 transition: color 250ms ease-out;
}
.SidebarOption--Active
{
    color: var(--twitter-color);
}
.SidebarOption > .MuiSvgIcon-root
{
    padding: 20px;
}
.SidebarOption > h2
{
    font-size: 20px;
    font-weight:800;
    margin-right: 20px;

}


.Sidebar
{
    border-right: 1px solid var(--twitter-background);
    flex: 0.25 1;
    min-width: 250xp;
    margin-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
.Sidebar--twittericon
{
    color: var(--twitter-color);
    padding-left: 20px;
    padding-right: 20px;
    font-size: 30px !important;

}
.Sidebar--tweet
{   
    background-color: var(--twitter-color) !important;
    border: none !important;
    color: white !important;
    font-weight: 900 !important;
    text-transform: inherit !important;
    border-radius: 30px !important;
    width: 100% !important;
    margin-left: 0.6% !important;
}
.Widgets
{
    flex: 0.25 1;
}
.Widgets--Input
{
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px;
}
.Widgets--Input > input
{
    border: none;
    background-color: var(--twitter-background);
}
.Widgets--SearchIcon
{
    color: gray;
}
.Widgets--WidgetContainer
{
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    background-color: #f5f8fa;
    border-radius: 20px;
}
.Widgets--WidgetContainer > h2
{
    font-size: 18px;
    font-weight: 800;
}



